.header {
  background-color: inherit;
  color: black;
  border-bottom: solid whitesmoke 4px;
  padding: 10px 80px;
}

.header .container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1400px;
  margin: auto;
}

.left {
  display: flex;
  align-items: center;
  gap: 30px;
  font-size: 20px;
}

.left > img {
  height: 40px;
}

.left > h1 {
  margin: 0;
  font-size: 30px;
}

.left > h1 > .blue {
  color: var(--color-blue);
}

.left > h1 > .red {
  color: var(--color-red);
}

.right > ul {
  display: flex;
  align-items: center;
  gap: 50px;
  font-size: 20px;
}

.header a {
  color: inherit;
  background-color: inherit;
  border: none;
  padding: 0;
  font-family: inherit;
  font-size: inherit;
}

.right a.active {
  text-decoration: underline;
  text-decoration-color: lightgrey;
}

.right a:hover {
  text-decoration: underline;
}